class HomePage extends Component {
  componentDidMount() {
    document.title = "خانه";
    window.addEventListener("scroll", this.handle_scroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  state = { burger_active: false, panel_number: 0 };

  burger_on_click_handler = event => {
    if (this.state.burger_active) {
      this.setState({ burger_active: false });
    } else {
      this.setState({ burger_active: true });
    }
  };

  handle_scroll = event => {
    var ds = window.pageYOffset;
    var of = window.innerHeight / 4;
    var panel = document.getElementsByClassName("panel");
    // if the panel is in the viewport, reveal the content, if not, hide it.
    var panel_number = 0;
    for (var i = 0; i < panel.length; i++) {
      if (panel[i].offsetTop < ds + of) {
        panel_number = i;
      } else {
        break;
      }
    }
    if (this.state.panel_number !== panel_number) {
      this.setState({ panel_number: panel_number });
    }
  };

  get_burger_class() {
    if (this.state.burger_active) {
      return " burger--active ";
    } else {
      return "";
    }
  }

  get_nav_class() {
    if (this.state.burger_active) {
      return " nav__list--active ";
    } else {
      return "";
    }
  }

  get_panel_content_class(n) {
    if (this.state.panel_number === n) {
      return " panel__content--active ";
    } else {
      return "";
    }
  }
  render() {
    return (
      <div className="main home_div text-right">
        <Header history={this.props.history} />
        <nav className="nav">
          <div
            className={"burger" + this.get_burger_class()}
            onClick={this.burger_on_click_handler}
          >
            <div className="burger__patty" />
          </div>

          <ul className={"nav__list" + this.get_nav_class()}>
            <li className="nav__item">
              <a href="#1" className="nav__link c-blue">
                <i class="fab fa-earlybirds" />
              </a>
            </li>
            <li className="nav__item">
              <a href="#2" className="nav__link c-yellow scrolly">
                <i className="far fa-lightbulb" />
              </a>
            </li>
            <li className="nav__item">
              <a href="#3" className="nav__link c-red">
                <i className="fas fa-question" />
              </a>
            </li>
            {/* <li className="nav__item">
              <a href="#4" className="nav__link c-green">
                <i className="fa fa-paper-plane" />
              </a>
            </li> */}
          </ul>
        </nav>

        <section className="panel b-blue" id="1">
          <article className="panel__wrapper" id="first_article">
            <div className={"panel__content" + this.get_panel_content_class(0)}>
              <h1 className="panel__headline">
                <i className="fab fa-earlybirds" />
                &nbsp; سناریو ۱
              </h1>
              <div className="panel__block" />
              <p>
                در این سناریو ارتباط افراد با طوطی مدنظر است و عکس‌العمل های
                کودک با طوطی ضبط می‌شود تا با استفاده از آنها تشخیص نهایی داده
                شود.
              </p>
              <div className="row">
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success">
                    روشن
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success ">
                    خاموش
                  </button>
                </div>
              </div>
              <div className="panel__spliter"></div>
              <h1 className="panel__headline">
                <i className="fab fa-earlybirds" />
                &nbsp; سناریو ۲
              </h1>
              <div className="panel__block" />
              <p>
                در این سناریو رفتار کودک در قبال اتفاقات محیط اهمیت دارد و
                براساس این رفتار ها نتیجه نهایی مشخص می‌شود.
              </p>
              <div className="row">
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success">
                    روشن
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success ">
                    خاموش
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success">
                    روشن
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success ">
                    خاموش
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success">
                    روشن
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success ">
                    خاموش
                  </button>
                </div>
              </div>
              <div className="panel__spliter"></div>
              <h1 className="panel__headline">
                <i className="fas fa-walking" />
                &nbsp; حرکات
              </h1>
              <div className="panel__block" />
              <p>
                در این بخش شما می‌توانید طوطی را به حرکت در بیاورید. چندین حرکت
                از پیش تعیین شده وجود دارد که می‌توانید از آنها هم استفاده کنید.
              </p>
              <div className="row">
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success">
                    روشن
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success ">
                    خاموش
                  </button>
                </div>
              </div>
            </div>
          </article>
        </section>
        <section className="panel b-yellow" id="2">
          <article className="panel__wrapper">
            <div className={"panel__content" + this.get_panel_content_class(1)}>
              <h1 className="panel__headline">
                <i className="far fa-lightbulb" />
                &nbsp; چرخ نور
              </h1>
              <div className="panel__block" />
              <p>
                چرخ گردان نورانی یکی دیگر از منابع اطلاعاتی سیستم است که شما
                می‌توانید آن را خاموش یا روشن کنید.
              </p>
              <div className="row">
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success">
                    روشن
                  </button>
                </div>
                <div className=" col-md-4 col-sm-6 col-xs-12 button_container">
                  <button type="button" className="btn btn-success ">
                    خاموش
                  </button>
                </div>
              </div>
            </div>
          </article>
        </section>
        <section className="panel b-red" id="3">
          <article className="panel__wrapper">
            <div className={"panel__content" + this.get_panel_content_class(2)}>
              <h1 className="panel__headline">
                <i className="fas fa-question" />
                &nbsp; در مورد ما
              </h1>
              <div className="panel__block" />
              <p>
                آزمایشگاه هوش مصنوعی با کمک جمعی از دانشجویان دانشگاه تهران و با
                استفاده از متولوژی های مدرن هوش مصنوعی در نظر دارد راهی برای
                تشخیص بیماری اوتیسم و بیماری های نظیر آن شود. این سیستم از چندین
                ربات و سنسور تشکیل شده که با کودک در ارتباط اند و عکس‌العمل های
                او را ضبط می‌کنند، در نهایت این اطلاعات پردازش شده و نتیجه نهایی
                مشخص می‌شود.
              </p>
            </div>
          </article>
        </section>
        {/* <section className="panel b-green" id="4">
          <article className="panel__wrapper">
            <div className={"panel__content"+this.get_panel_content_class(3)}>
              <h1 className="panel__headline">
                <i className="fa fa-paper-plane" />&nbsp;Paper Planes
              </h1>
              <div className="panel__block" />
              <p>
                90's wayfarers lomo you probably haven't heard of them trust
                fund banh mi. Flannel Shoreditch dreamcatcher, quinoa
                flexitarian Banksy pickled post-ironic lo-fi. Photo booth
                asymmetrical tousled letterpress.
              </p>
            </div>
          </article>
        </section> */}
      </div>
    );
  }
}

